import { Directive, Host, HostBinding} from '@angular/core';
import { MatFormField } from '@angular/material/form-field';

// tslint:disable:directive-selector
@Directive({
  selector: '[boxStyle]',
  exportAs: 'boxStyle'
})
export class BoxedInputDirective {
   @HostBinding('attr.fisStyle') fisStyle = '';
   constructor(@Host() private matFormField: MatFormField) {
     matFormField.appearance = 'outline';
  }
}
